  .container {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
      gap: 20px;
      max-width: 1200px;
      padding: 20px;
  }

  .card {
      background-color: white;
      border-radius: 10px;
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
      overflow: hidden;
      transition: transform 0.3s ease;
  }

  .card:hover {
      transform: translateY(-5px);
  }

  .card img {
      width: 100%;
      height: auto;
      display: block;
  }

  .card-body {
      padding: 15px;
      text-align: center;
  }

  .card-body h3 {
      margin: 10px 0;
      font-size: 1.2rem;
  }

  .card-body p {
      margin: 10px 0;
      color: #777;
      font-size: 0.9rem;
  }

  .card-body a {
      display: inline-block;
      padding: 10px 20px;
      background-color: #007bff;
      color: white;
      border-radius: 5px;
      text-decoration: none;
      transition: background-color 0.3s ease;
  }

  .card-body a:hover {
      background-color: #0056b3;
  }