
.sign-in-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
    margin: 20px auto;
    /* Center horizontally and add vertical margin */
    padding: 20px;
    /* Add padding inside the box */
    background-color: #1e1e1e;
    /* Dark grey background for contrast */
    border-radius: 10px;
    /* Rounded corners */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    /* Soft shadow for depth */
    width: 90%;
    /* Responsive width */
    max-width: 400px;
    /* Limit max width for larger screens */
}

.sign-in-btn {
    width: 250px;
    padding: 10px 20px;
    border: 2px solid #ddd;
    border-radius: 30px;
    background-color: white;
    font-size: 16px;
    color: #000;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.sign-in-btn .icon {
    height: 24px;
    width: 24px;
}

.sign-in-btn:hover {
    background-color: #f5f5f5;
    border-color: #bbb;

}

/* Individual button styles */
.google-btn .icon {
    filter: none;
    background-color: transparent;
}

.facebook-btn .icon {
    filter: none;
    background-color: transparent;
}

.apple-btn .icon {
    filter: grayscale(100%);
    background-color: transparent;
}

.twitter-btn .icon {
    filter: none;
    background-color: transparent;
}

.email-btn .icon {
    filter: none;
    background-color: transparent;
}